.form {
  max-width: 450px;
  min-width: 350px;
  min-height: 400px;
  border:#ccc 1px solid;
  border-radius: 12px;
  background-color: #fff;
  margin:0 20px;
}
.loading{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position:absolute;
  left: 0;
  top:0;
  right:0;
  bottom:0;
}
.loading-rotulo{
  width:100%;
  height:100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.line {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background-color: #4b9cdb;
  margin:2px
}
.load-2 .line:nth-last-child(1) {
  animation: loadingB 1.5s 1s infinite;
}
.load-2 .line:nth-last-child(2) {
  animation: loadingB 1.5s 0.5s infinite;
}
.load-2 .line:nth-last-child(3) {
  animation: loadingB 1.5s 0s infinite;
}
@keyframes loadingB {
  0% {
    width: 15px;
  }
  50% {
    width: 35px;
  }
  100% {
    width: 15px;
  }
}

.form-logo {
  pointer-events: none;
}

.form-header {
  font-size:20px;
  font-weight: 700;
  padding:20px 20px 10px;
}

.form-body{
 padding:0 20px
}
.form-group{
  margin-bottom: 15px;
}

.btn-theme{
  background-color: #053461;
  color: #fff;
  font-weight: 700;
  width: 100%;
}

.text-small{
  font-size: 13px;
}